.search {
  width: 100%;
  height: 40px;
  border-radius: 10px;
  display: flex;
  box-shadow: 0 1px 2px #00000040;
}

.search__region {
  width: 30%;
  background: #f0f0f0;
  border-radius: 10px 0 0 10px;
  justify-content: center;
  align-items: center;
  gap: 5px;
  display: flex;
}

.search__region p {
  color: #1b89ce;
  font-size: 14px;
}

select {
  color: #1b89ce;
  font-size: 14px;
  font: inherit;
  background: none;
  border: none;
  outline: none;
}

.arrow_down {
  content: "";
  width: 0;
  height: 0;
  border-top: 8px solid #1b89ce;
  border-left: 7px solid #0000;
  border-right: 7px solid #0000;
  display: block;
}

.search__name {
  width: 100%;
  background: #fff;
  border-radius: 0 10px 10px 0;
  justify-content: center;
  align-items: center;
  display: flex;
}

.search__name input {
  width: 100%;
  height: 100%;
  color: #131313;
  border: none;
  border-radius: 0 10px 10px 0;
  padding: 10px;
  font-size: 16px;
}

input:focus {
  outline: none;
}

input::placeholder {
  color: #777;
}

.search_icon {
  visibility: hidden;
  margin-right: 15px;
}

.search_icon--active {
  visibility: visible;
}

.search_region--active {
  color: #fff;
  background: #1b89ce;
}

.tooltip {
  margin-top: 20px;
  margin-left: var(--padding-left);
  height: 43px;
  display: inline-block;
  position: relative;
}

.tooltip__text {
  font-size: 9px;
}

.tooltip .tooltip__text {
  width: 300px;
  z-index: 1;
  color: #131313;
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
  background-color: #ececec;
  border-radius: 6px;
  padding: 10px;
  line-height: .9rem;
  transition: all .2s;
  position: absolute;
  top: -35%;
  left: 100%;
  transform: translateY(5px);
}

.tooltip .tooltip__text:after {
  content: "";
  border: 5px solid #0000;
  border-right-color: #ececec;
  margin-top: -5px;
  position: absolute;
  top: 50%;
  right: 100%;
}

.tooltip:hover .tooltip__text {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

section {
